import { useState } from 'react'
import { Col, NavLink, Button } from "react-bootstrap"
import 'bootstrap/dist/css/bootstrap.min.css'
import './header.css'
import logoSVG from '../../scss/svg/logo.svg'
import burgerSVG from '../../scss/svg/burger-img.svg'
import closeSVG from '../../scss/svg/close-btn.svg'

const linkList = [
  { name: "Home", href: '#top' },
  { name: "Goals", href: '#goals' },
  { name: "Plans", href: '#plan' },
  { name: "FAQ", href: '#faq' },
]

export const HeaderComponent = () => {
  const [menu_class, setMenuClass] = useState('hidden-burger-menu')
  const [isMenuClicked, setIsMenuClicked] = useState(false)

  const openMenu = () => {
    if (!isMenuClicked) {
      setMenuClass("visible-burger-menu")
    } else {
      setMenuClass("hidden-burger-menu")
    }
  }

  const closeMenu = () => {
    if (isMenuClicked) {
      setMenuClass("visible-burger-menu")
    } else {
      setMenuClass("hidden-burger-menu")
    }
  }

  return (
    <header className="bg-white">
      <div className='d-flex align-items-center justify-content-md-around justify-content-between mx-3 mx-md-5 py-3'>
        <Col md={4} className="align-items-center d-flex">
          <img src={logoSVG} alt="enrex logo" />
          <h5 className="gray-color ms-2 mb-0">
            ENREX
          </h5>
        </Col>
        <Col className="d-md-none d-flex justify-content-end" onClick={openMenu}>
          <img src={burgerSVG} alt="burger img" />
        </Col>
        <Col md={4} className="d-md-flex align-items-center d-none justify-content-center">
          {linkList.map(({ name, href }) => <NavLink href={href} className="gray-color" key={name}>{name}</NavLink>)}
        </Col>
        <Col md={4} className="d-md-flex d-none align-items-center justify-content-end">
          <Button variant='light' className="btn py-2 btn-outline-dark px-4 me-4">DEMO</Button>
          <Button className="px-4 py-2">GREEN YOUR WEB</Button>
        </Col>
      </div>
      <div className={`burger-menu py-4 pt-1 d-md-none ${menu_class}`}>
        <div className='d-flex justify-content-end px-3' onClick={closeMenu}>
          <img src={closeSVG} alt="close img" />
        </div>
        {linkList.map(({ name, href }) => <NavLink href={href} className="gray-color" key={name}>{name}</NavLink>)}
      </div>
    </header>
  )
}
