import { Accordion } from "react-bootstrap";
import { AccordionItem } from "./accordionItem";
import "./accordionComponent.css";

const accordionDataFirst = [
  { title: "About Enrex", text: "Enrex is a company that works with environmental friendly solutions, offsets, government mandated certificates. We offer solution as Greening websites, offsetting NFT's and Businesses." },
  { title: "How to add the Widget?", text: 'Follow the steps in "How it works" and visit our app on - https://greenweb.enrex.io/home If you still have questions please book a demo here - https://calendly.com/ dainiusenrex/introduction-call' },
  { title: "How does the Widget work?", text: "Once you add your widget to you webpage we calculate the energy consumption of your webpage visitors. You can always log in to your account to see the energy calculations. Each month we Offset (purchase equal amount of renewable energy)  your Webpage used electricity and issue you a personal certificate to prove that your webpage is carbon neutral and completely Green!" },
  { title: "What happens if I go above the monthly visit count of my plan?", text: "If you do not reach 10,000 visits per month your plan will stay Free. If you go above the 10,000 visits then you will need to subscribe to one of the subscription plans. All the plans have the visit counts mentioned. If you reach above the plan visit count you will have 2 weeks to upgrade your plan to a higher tier. Take a look at our subscription plans." },
  { title: "Does the Widget collect data about my visitors or my webpage?", text: "No. We do not collect data about the visitors, we only calculate how many visitors arrived to your webpage and their energy consumption." },
  { title: "What kind of certificates does Enrex provide?", text: "Renewable energy certificates (REC in short) this is an official document which proves that your energy consumption is covered with Green energy." },
  { title: "What does it mean to my company to have a certificate?", text: "It means your website is Green, your energy consumption is covered with renewable energy. From our research we can see that visitor highly appreciate companies that care for the environment. They return to the webpage more often and convert more easily. Also your are helping the planet stay a better place for us and future generations." }
];

const accordionDataSecond = [
  { title: "First", text: 'Some text' },
  { title: "Second", text: 'Some text' }
];

const accordionDataThird = [
  { title: "Third block first", text: 'Some text' },
  { title: "Third block second", text: 'Some text' }
];

export const AccordionComponent = () => {
  return (
    <Accordion flush className="pb-5" alwaysOpen>
      <Accordion.Item eventKey="0">
        <Accordion.Header>ABOUT ENREX</Accordion.Header>
        <Accordion.Body className="p-0 px-md-3" data-bs-target="#panelsStayOpen-collapseOne">
          <div className="roboto-fs">
            {accordionDataFirst.map(({ title, text }) => (
              <AccordionItem title={title} text={text} key={title} />
            ))}
          </div>
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="1">
        <Accordion.Header>CARBON MARKET</Accordion.Header>
        <Accordion.Body className="p-0 px-md-3">
          <div>
            {accordionDataSecond.map(({ title, text }) => (
              <AccordionItem title={title} text={text} key={title} />
            ))}
          </div>
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="2">
        <Accordion.Header>BLOCKCHAIN AND $ENRX TOKEN</Accordion.Header>
        <Accordion.Body className="p-0 px-md-3">
          <div>
            {accordionDataThird.map(({ title, text }) => (
              <AccordionItem title={title} text={text} key={title} />
            ))}
          </div>
        </Accordion.Body>
      </Accordion.Item>
    </Accordion>
  )
}
