import { CardImg } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css'
import frame1 from '../../scss/svg/frame1.svg'
import frame2 from '../../scss/svg/frame2.svg'
import frame3 from '../../scss/svg/frame3.svg'
import './greenTraffic.css'

export const GreenTraffic = () => {
  return (
    <div className="align-items-center d-flex flex-column mt-5 pb-5">
      <h1 className="mb-4 mt-5  mx-3 gray-color text-center">GREEN TRAFFIC FOR BETTER RESULTS</h1>
      <div className="d-flex px-3 px-md-5 align-items-center flex-column flex-md-row justify-content-between">
            <div className="card-traffic width-32 card-bg">
              <div>
                <CardImg className="card-img-top card-mage mt-4" src={frame1} alt="Card image cap" />
              </div>
                <div className="card-body">
                  <h4 className="card-title gray-color mb-3">UNLOCK NEW POTENTIAL</h4>
                  <p className="card-text roboto-fs">
                    34% of consumers are willing to pay an average of <b>25% more </b> for sustainable products. People <b>care for the environment</b> and theyre voting with their wallets.
                  </p>
                </div>
            </div>
        <div className="card-traffic width-32 card-bg">
          <div>
            <CardImg className="card-img-top card-mage mt-4" src={frame2} alt="Card image cap" />
          </div>
          <div className="card-body">
            <h4 className="card-title gray-color mb-3">GROW CONVERSIONS</h4>
            <p className="card-text roboto-fs">
              You <b>become more</b> than just a product or a service - you become more sustainable in the eyes of your clients. That is why they <b>will return</b> and spend more with you!
            </p>
          </div>
        </div>
        <div className="card-traffic width-32 card-bg">
          <div>
            <CardImg className="card-img-top card-mage mt-4" src={frame3} alt="Card image cap" />
          </div>
        <div className="card-body">
          <h4 className="card-title gray-color mb-3">BUILD GREEN TRUST</h4>
          <p className="card-text roboto-fs">
            Your brand will have <b>a credible impact</b> on the fight against climate change, and your visitors <b>will notice</b>. If you <b>go green</b>, your clients will appreciate it by visiting your website <b>more often.</b>
          </p>
        </div>
      </div>
      </div>
    </div>
  )
}
