import { Button, Col } from "react-bootstrap"
import mainWidget from '../../scss/svg/mainWidget.svg'
import { MainWidget} from "./MainWidget"

import fullWidgetPNG from "../../scss/png/full_widget.png";

export const PresentationBlock = () => {
  return (
    <div className="d-flex flex-md-row flex-column py-5 presentation-bg">
      <Col className="ms-0 ms-md-5 d-flex flex-column align-items-center justify-content-center">
        <h2 className="text-uppercase text-md-start text-center text-white">
          Boost your sites traffic
          and conversions by making
          your website green
        </h2>
        <p className="align-self-md-start text-white align-self-center">Join the Enrex Green Web cause</p>
        <Button variant='light' className="btn btn-outline-dark align-self-md-start align-self-center mb-4 px-5 px-md-4 border-0 text-uppercase gray-color">green your web</Button>
      </Col>
      <Col className="me-0 me-md-5">
        {/*<div style={{height: '489px', width: '298px'}}>*/}
        {/*  <CardImg style={{objectFit: "contain"}} className="w-100 h-100 border-5 main-widget" src={mainWidget} />*/}
        {/*</div>*/}
        {/* <MainWidget /> */}
        <img className="mw-100" src={fullWidgetPNG} alt="img"/>
      </Col>
    </div>
  )
}