import './subscriptionComponent.css';
import ArrowLeftSVG from '../../scss/svg/Arrow-left.svg';
import { CardComponent } from '../common/card/cardComponent';
import { useState } from 'react';
import { Carousel } from 'react-bootstrap';

const cardsMonthlyList = [
  { title: 'FREEMIUM', list: [{ first: '0-10k visits/month', second: 'Group certificate' }], price: '0$/', date: 'month', btnValue: 'GET STARTED', sale: false },
  { title: 'STARTER', list: [{ first: '10k-100k visits/month', second: 'Personalized certificate' }], price: '19$/', date: 'month', btnValue: 'GET STARTED', sale: false },
  { title: 'ADVANCED', list: [{ first: '100k-500k visits/month', second: 'Personalized certificate' }], price: '79$/', date: 'month', btnValue: 'GET STARTED', sale: false },
  { title: 'ENTERPRISE', list: [{ first: '500k+ visits/month', second: 'Personalized certificate' }], price: '', date: '', btnValue: 'GET STARTED', sale: false },
];

const cardsYearlyList = [
  { title: 'FREEMIUM', list: [{ first: '0-10k visits/month', second: 'Group certificate' }], price: '0$/', oldPrice: '', date: 'year', btnValue: 'GET STARTED', sale: false },
  { title: 'STARTER', list: [{ first: '10k-100k visits/month', second: 'Personalized certificate' }], price: '171$/', oldPrice: '228', date: 'year', btnValue: 'GET STARTED', sale: true },
  { title: 'ADVANCED', list: [{ first: '100k-500k visits/month', second: 'Personalized certificate' }], price: '711$/', oldPrice: '948', date: 'year', btnValue: 'GET STARTED', sale: true },
  { title: 'ENTERPRISE', list: [{ first: '500k+ visits/month', second: 'Personalized certificate' }], price: '', oldPrice: '', date: '', btnValue: 'CONTACT US', sale: true },
];

export const SubscriptionComponent = () => {
  const [selectedRadioBtn, setSelectedRadioBtn] = useState("monthly");

  const isRadioSelected = (value) => selectedRadioBtn === value;
  const handleRadioClick = (e) => setSelectedRadioBtn(e.currentTarget.value);

  return (
    <div className='subscription-block'>
      <div className='subscription-block-wrapper d-flex justify-content-center text-center pt-5'>
        <div>
          <h1 className="white-color text-uppercase mt-5 mx-2">One world - one simple plan</h1>
          <p className="fw-bold white-color">Traffic-based freemium model</p>
          <div className='position-relative'>
            <div className="btn-group m-3 text-center">
              <input type="radio"
                className="btn-check"
                name="options"
                id="radio1"
                autoComplete="off"
                value="monthly"
                checked={isRadioSelected("monthly")}
                onChange={handleRadioClick}
              />
              <label className="btn-monthly btn btn-outline-secondary roboto-fs" htmlFor="radio1">Monthly</label>
              <input type="radio"
                className="btn-check"
                name="options"
                id="radio2"
                autoComplete="off"
                value="yearly"
                checked={isRadioSelected("yearly")}
                onChange={handleRadioClick}
              />
              <label className="btn-yearly btn-monthly btn btn-outline-secondary px-4 roboto-fs" htmlFor="radio2">Yearly</label>
            </div>
            <a className='d-md-inline d-none text-decoration-none' href='#'><img src={ArrowLeftSVG} alt="arrow img" /> Save 25% with our yearly plan!</a>
          </div>

        </div>
      </div>
      {selectedRadioBtn === "monthly" &&
        <div className='d-none d-md-flex justify-content-md-center'>
          {cardsMonthlyList.map(({ title, list, price, date, btnValue, sale }) => (
            <CardComponent
              title={title}
              first={list[0].first}
              second={list[0].second}
              price={price}
              date={date}
              btnValue={btnValue}
              sale={sale}
              key={title} />
          ))}
        </div>
      }

      {selectedRadioBtn === "yearly" &&
        <div className='d-none d-md-flex justify-content-md-center'>
          {cardsYearlyList.map(({ title, list, price, oldPrice, date, btnValue, sale }) => (
            <CardComponent
              title={title}
              first={list[0].first}
              second={list[0].second}
              price={price}
              oldPrice={oldPrice}
              date={date}
              btnValue={btnValue}
              sale={sale}
              key={title} />
          ))}
        </div>
      }
      {selectedRadioBtn === "monthly" &&
        <div className="d-md-none">
          <Carousel>
            {cardsMonthlyList.map(({ title, list, price, oldPrice, date, btnValue, sale }) => (
              <Carousel.Item>
                <CardComponent
                  title={title}
                  first={list[0].first}
                  second={list[0].second}
                  price={price}
                  oldPrice={oldPrice}
                  date={date}
                  btnValue={btnValue}
                  sale={sale}
                  key={title} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      }
      {selectedRadioBtn === "yearly" &&
        <div className="d-md-none">
          <Carousel>
            {cardsYearlyList.map(({ title, list, price, oldPrice, date, btnValue, sale }) => (
              <Carousel.Item>
                <CardComponent
                  title={title}
                  first={list[0].first}
                  second={list[0].second}
                  price={price}
                  oldPrice={oldPrice}
                  date={date}
                  btnValue={btnValue}
                  sale={sale}
                  key={title} />
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      }
      <div className='d-none d-md-grid'>
        <div></div>
        <div className='subscription-block-border-bottom pt-5'></div>
      </div>
      <div id="faq"/>
    </div>
  )
};