import { CardImg, Carousel, CarouselItem } from "react-bootstrap"
import calculate  from '../../scss/svg/calculate.svg'
import estimate  from '../../scss/svg/estimate.svg'
import leaf  from '../../scss/svg/leaf.svg'
import './offsetting.css'


export const Offsetting = () => {
  return (
    <div className="position-relative">
    <div className="card-offsetting position-absolute top-0 start-0" />
    <div className="d-flex align-items-center flex-column mt-5">
      <h1 className="gray-color text-center">OFFSETTING EXPLAINED</h1>
      <small className="fw-bold gray-color roboto-fs mx-4 text-center">Empower your visitors to drive growth in the green energy sector</small>
      <div className="d-none d-md-flex mx-5 my-5 align-items-md-center justify-content-md-between">
        <div className="card-width col-4">
              <div className="mt-4">
                <CardImg className="card-img-top card-offset" src={calculate} alt="Card image cap" />
              </div>
              <div className="card-body">
                <p className="card-text gray-color roboto-fs">
                  <b>We calculate</b> the energy each visitor consumes while visiting your page
                </p>
              </div>
        </div>
        <div className="card-width col-4">

          <div className="mt-4">
            <CardImg className="card-img-top card-offset" src={estimate} alt="Card image cap" />
          </div>
          <div className="card-body">
            <p className="card-text gray-color roboto-fs">
              You get quarterly reports <b>to see how much</b> energy your website and visitors consumed so that Enrex <b>can take action.</b>
            </p>
          </div>
        </div>
        <div className="card-width">
          <div className="mt-4">
            <CardImg className="card-img-top card-offset" src={leaf} alt="Card image cap" />
          </div>
          <div className="card-body">
            <p className="card-text gray-color roboto-fs">
              We use <b>renewable energy</b> certificates from renewable energy power plants <b>to cover</b> your consumption.
            </p>
          </div>
        </div>
      </div>
      <div className="d-md-none mb-4">
        <Carousel interval={null}>
          <CarouselItem>
            <div className="card-width m-4 mb-5">
              <div className="pt-4">
                <CardImg className="card-img-top card-offset" src={calculate} alt="Card image cap" />
              </div>
              <div className="card-body">
                <p className="card-text roboto-fs">
                  <b>We calculate</b> the energy each visitor consumes while visiting your page
                </p>
              </div>
            </div>
          </CarouselItem>
          <CarouselItem>
            <div className="card-width m-4 mb-5">
              <div className="pt-4">
                <CardImg className="card-img-top card-offset" src={estimate} alt="Card image cap" />
              </div>
              <div className="card-body">
                <p className="card-text roboto-fs">
                You get quarterly reports <b>to see how much</b> energy your website and visitors consumed so that Enrex <b>can take action.</b>
                </p>
              </div>
            </div>
          </CarouselItem>
          <CarouselItem>
            <div className="card-width m-4 mb-5">
              <div className="pt-4">
                <CardImg className="card-img-top card-offset" src={leaf} alt="Card image cap" />
              </div>
              <div className="card-body">
                <p className="card-text roboto-fs">
                We use <b>renewable energy</b> certificates from renewable energy power plants <b>to cover</b> your consumption.
                </p>
              </div>
            </div>
          </CarouselItem>
        </Carousel>
      </div>
      <a className="text-decoration-underline roboto-fs gray-color d-none d-md-block" href="https://medium.com/@enrex/methodology-for-calculating-carbon-emissions-in-the-enrex-web-offsetting-widget-520bba4d0ba8">Learn more</a>
    </div>
    </div>
  )
}
