import logo1 from '../../scss/svg/logoList/logo1.svg'
import logo2 from '../../scss/svg/logoList/logo2.svg'
import logo3 from '../../scss/svg/logoList/logo3.svg'
import logo4 from '../../scss/svg/logoList/logo4.svg'
import logo5 from '../../scss/svg/logoList/logo5.svg'
import logo6 from '../../scss/svg/logoList/logo6.svg'
import logo7 from '../../scss/svg/logoList/logo7.svg'
import logo8 from '../../scss/svg/logoList/logo8.svg'
import logo9 from '../../scss/svg/logoList/logo9.svg'
import logo10 from '../../scss/svg/logoList/logo10.svg'
import logo11 from '../../scss/svg/logoList/logo11.svg'
import './logoBlock.css'

const logoIconList = [
  logo1, logo2, logo3, logo4,logo5, logo6, logo7, logo8, logo9, logo10, logo11
]
export const LogoBlock = () => {
  return (
    <div className="d-flex logo-block overflow-auto mx-md-5 mx-3 d-md-flex justify-content-between align-items-center my-4">
      {logoIconList.map(icon => {
        return <img className="mx-4 mx-md-0" key={icon} src={icon} />
      })}
    </div>
  )
}
