import illustrationSVG from "../../scss/svg/join-block-illustration.svg"
import { AccordionComponent } from "../common/accordion/AccordionComponent"

export const FAQComponent = () => {
  return (
    <div className="d-flex justify-content-center text-center px-5 mt-5">
      <div className="col-md-8">
        <h1 className="gray-color d-md-none text-uppercase mt-5 fs-3">Frequently Asked questions</h1>
        <h1 className="gray-color d-none d-md-block text-uppercase mt-5 fs-1">Frequently Asked questions</h1>
        <p className="fw-bold gray-color">Want to ask something from us?</p>
        <AccordionComponent />
        <div className="d-md-flex justify-content-md-between mt-md-5">
          <div className="d-md-none my-5">
            <h1 className="gray-color text-uppercase text-nowrap fs-3">Join the movement</h1>
            <button type="button" className="btn btn-primary align-self-start col-12">green your web</button>
          </div>
    
          <div>
            <img className="mw-100" src={illustrationSVG} alt='illustration' />
          </div>
          <div className="d-flex align-items-center">
            <div className="d-md-flex flex-md-column d-none">
              <h1 className="gray-color text-uppercase text-nowrap text-start">Join the movement</h1>
              <button type="button" className="btn btn-primary align-self-start">green your web</button>
            </div>
          </div>

        </div>
      </div>
    </div>
  )
}
