import './App.css'
import {HeaderComponent} from './components/headerComponent/HeaderConponent'
import {PresentationBlock} from './components/presentationBlock/PresentationBlock'
import {HowItWorks} from "./components/howItWorks/HowItWorks"
import {LogoBlock} from "./components/logoBlock/LogoBlock"
import {GreenTraffic} from "./components/greenTraffic/GreenTraffic"
import {HowItLooks} from "./components/howItLooks/HowItLooks"
import {Offsetting} from "./components/offsetting/Offsetting"
import {BookDemo} from "./components/bookDemo/BookDemo";
import {FAQComponent} from './components/FAQBlock/FAQComponent';
import {FooterComponent} from "./components/footerComponent/FooterComponent";
import { SubscriptionComponent } from './components/subscriptionComponent/subscriptionComponent'

function App() {
  return (
    <>
        <HeaderComponent />
        <PresentationBlock />
        <LogoBlock />
        <HowItWorks />
        <GreenTraffic />
        <div className='d-block d-md-none'>
          <Offsetting/>
        </div>
        <HowItLooks />
        <div className='d-none d-md-block'>
          <Offsetting/>
        </div>
        <BookDemo />
        <SubscriptionComponent />
        <FAQComponent />
        <FooterComponent />
    </>
  )
}

export default App
