import {Button, Col} from "react-bootstrap"
import imageDemo from '../../scss/svg/illustrationDemo.svg'

export const BookDemo =() => {
  return (
    <>
      <div className="d-flex align-items-center justify-content-center flex-column flex-md-row my-5">
        <Col className="justify-content-end d-flex me-md-5 me-0">
          <img src={imageDemo} className="w-100" />
        </Col>
        <Col className="text-center text-md-start mt-4 mt-md-0">
          <h1 className="gray-color">BOOK A DEMO</h1>
          <p>Be the first to unlock the environmental future with enrex</p>
          <Button className="px-4 py-2">GET DEMO</Button>
        </Col>
      </div>
      <div id='plan'/>
    </>
  )
}
