import { useState } from 'react';
import './accordionItem.css';
import plusSVG from '../../../scss/svg/math-plus.svg';
import minusSVG from '../../../scss/svg/math-minus.svg';

export const AccordionItem = ({title, text}) => {

    const [isActive, setIsActive] = useState(false);

    return (
        <div className='accordion-wrapper my-3 text-start'>
            <div 
                className='accordion-title d-flex justify-content-between p-3'
                onClick={() => setIsActive(!isActive)} 
                >
                    {isActive ?
                        <>
                            <div>{title}</div>
                            <img src={minusSVG} alt="minus img"/>
                        </> :
                        <>
                            <div className='title-color'>{title}</div>
                            <img src={plusSVG} alt=" img" />
                        </>
                    }
            </div>

            {isActive && <div className='accordion-content text-start p-3'>{text}</div>}

        </div>
    )
}