import { useState } from 'react';
import './cardComponent.css';
import cardSVG from '../../../scss/svg/card-img.svg';
import tickSVG from '../../../scss/svg/tick.svg';

export const CardComponent = ({title, first, second, price, oldPrice , date, btnValue, sale}) => {

    const [isActive, setIsActive] = useState(false);

    return (
        <div className={isActive ? "card-item-active position-relative mx-3 my-5" : "card-item position-relative mx-3 my-5"} 
            onMouseEnter={() => setIsActive(!isActive)}
            onMouseLeave={() => setIsActive(!isActive)}
            >
            {sale && <span class="sale-badge badge bg-danger rounded-pill p-2">Save 25%</span>}
            <div className="text-center">
                <div className="m-4"><img src={cardSVG} alt='card img'/></div>
                <div className="card-title arkhip-fs">{title}</div>
                <div className="card-description roboto-fs text-start mx-3"><img src={tickSVG} alt='tick img ' className='px-2'/>{first}</div>
                <div className="card-description roboto-fs text-start mx-3"><img src={tickSVG} alt='tick img' className='px-2'/>{second}</div>
                <div className="card-price arkhip-fs mt-4">{price}<span className='card-span roboto-fs'><s>{oldPrice}</s>{date}</span></div>        
                {isActive 
                    ? <button className='btn btn-primary mx-5 my-4' >{btnValue}</button>
                    : <button className='btn btn-outline-light mx-5 my-4' >{btnValue}</button>
                }
            </div>
        </div>
    )
}